import React from 'react'

const ImportItemData = React.lazy(() => import('./views/pricebook/ImportItemData'))
const AppendItemData = React.lazy(() => import('./views/pricebook/AppendItemData'))
const VersionData = React.lazy(() => import('./views/pricebook/VersionData'))
const QuotationList = React.lazy(() => import('./views/quoteToCash/QuotationList'))
const Quotation = React.lazy(() => import('./views/quoteToCash/Quotation'))
const PdfContainer = React.lazy(() => import('./views/quoteToCash/PdfContainer'))
const QuotationReview = React.lazy(() => import('./views/quoteToCash/QuotationReview'))
const QuotationComplete = React.lazy(() => import('./views/quoteToCash/QuotationComplete'))
const QuotationEnd = React.lazy(() => import('./views/quoteToCash/QuotationEnd'))
const QuotationReport = React.lazy(() => import('./views/quoteToCash/QuotationReport'))

const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/quotation/index', name: '報價單列表', element: QuotationList },
  { path: '/quotation/:childPath', name: '報價單列表', element: Quotation },
  { path: '/import_item_data', name: 'MS價格冊整批匯入', element: ImportItemData },
  { path: '/append_item_data', name: 'MS價格冊新增項目', element: AppendItemData },
  { path: '/version_data', name: '價格冊版本資訊', element: VersionData },
  { path: '/quotation_review', name: '審核列表', element: QuotationReview },
  { path: '/quotation_complete', name: '成案列表', element: QuotationComplete },
  { path: '/quotation_end', name: '終止列表', element: QuotationEnd },
  { path: '/quotation_report', name: '報表查詢功能', element: QuotationReport },
  { path: '/pdfContainer', name: 'pdf', element: PdfContainer },
]

export default routes
